import React, { Component } from "react";
import { Input } from 'antd';
const InputGroup = Input.Group;

class View extends Component {

  onChange = (index, e) => {
    let data = this.props.value || []
    let isNumber = this.props.isNumber
    if(Array.isArray(data)){
      let value = e.currentTarget.value
      data[index] = isNumber ? (isNaN(Number(value)) ? undefined : Number(value)) : value
    }
    console.log(data);
    const { onChange } = this.props
    if(onChange) onChange(data.filter(item => item !== undefined))
  };

  render() {
    const { value=[], onChange, placeholder=[], ...otherProps } = this.props
    return (
      <InputGroup {...otherProps}>
        <Input style={{ width: 150, textAlign: 'center' }} 
          placeholder={placeholder.length ? placeholder[0] : "下限"} 
          value={(Array.isArray(value) && value.length) ? value[0] : ''} 
          onChange={this.onChange.bind(this, 0)}
        />
        <Input
          style={{
            width: 30,
            borderLeft: 0,
            pointerEvents: 'none',
            backgroundColor: '#fff',
          }}
          placeholder={placeholder.length > 0 ? placeholder[1] : "~"} 
          disabled
        />
        <Input style={{ width: 150, textAlign: 'center', borderLeft: 0 }} 
          placeholder={placeholder.length > 1 ? placeholder[2] : "上限"} 
          value={(Array.isArray(value) && value.length > 0) ? value[1] : ''} 
          onChange={this.onChange.bind(this, 1)}
        />
      </InputGroup>
    );
  }
}
export default View